import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import photo_light_down from '@/components/photo_light_down'
import photo_light_top from '@/components/photo_light_top'
import photo_noLight_top from '@/components/photo_noLight_top'
import photo_noLight_down from '@/components/photo_noLight_down'
import paint_light_down from '@/components/paint_light_down'
import paint_light_top from '@/components/paint_light_top'
import paint_noLight_top from '@/components/paint_noLight_top'
import paint_noLight_down from '@/components/paint_noLight_down'


Vue.use(Router)

export default new Router({
  routes: [{
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/photo_light_down',
      name: 'photo_light_down',
      component: photo_light_down
    },
    {
      path: '/photo_light_top',
      name: 'photo_light_top',
      component: photo_light_top
    },
    {
      path: '/photo_noLight_top',
      name: 'photo_noLight_top',
      component: photo_noLight_top
    },
    {
      path: '/photo_noLight_down',
      name: 'photo_noLight_down',
      component: photo_noLight_down
    },
    {
      path: '/paint_light_down',
      name: 'paint_light_down',
      component: paint_light_down
    }, {
      path: '/paint_light_top',
      name: 'paint_light_top',
      component: paint_light_top
    }, {
      path: '/paint_noLight_top',
      name: 'paint_noLight_top',
      component: paint_noLight_top
    }, {
      path: '/paint_noLight_down',
      name: 'paint_noLight_down',
      component: paint_noLight_down
    },
  ]
})



