<template>
    <div>
        <v-row class="cus_card">
            <v-col cols="6" class="col_padding">
                <draggable class="list-group" :list="list1" group="people" @start="drag=true" @end="drag=false">
                    <v-list-item-group v-for="element in list1" :key="element.name" v-on:click.native="showpop(element)" data-app="true">
                        <v-list-item-title><span>{{ element.name }}</span></v-list-item-title>
                        {{element.info}}
                        <v-img class="paint" :src="element.img_paint" />
                        <div class="task nolight">
                            {{ element.task }}
                        </div>
                    </v-list-item-group>
                </draggable>
            </v-col>
            <v-col cols="6" class="col_padding">
                <draggable class="list-group" :list="list2" group="people" @start="drag=true" @end="drag=false">
                    <v-list-item-group v-for="element in list2" :key="element.name" v-on:click.native="showpop(element)" data-app="true">
                        <v-list-item-title><span>{{ element.name }}</span></v-list-item-title>
                        {{element.info}}
                        <v-img class="paint" :src="element.img_paint" />
                        <div class="task nolight">
                            {{ element.task }}
                        </div>
                    </v-list-item-group>
                </draggable>
            </v-col>
        </v-row>
        <v-dialog v-model="dialog" max-width="290">
            <v-card>
                <v-list-item-title><span>{{dialogName}}</span></v-list-item-title>
                {{dialogInfo}}
                <v-img :src="dialogImgPaint" />
                <div class="task nolight">
                    {{dialogTask}}
                </div>
            </v-card>
        </v-dialog>
    </div>
</template>

<script>
import draggable from "vuedraggable";
export default {
    name: "photo_nolight_down",
    components: {
        draggable
    },
    data() {
        return {
            dialog: false,
            lineHigh: false,
            dialogName: "",
            dialogId: "",
            dialogInfo: "",
            dialogTask: "",
            dialogImgPhoto: "",
            dialogImgPaint: "",
            list1: [
                {
                    name: "花磚池",
                    id: 1,
                    info: "由於早期男尊女卑的觀念，女性只能在花磚小池子泡湯。",
                    task:
                        "用自己身體局部與池中的彩色花磗合照，並在臉書公開打卡，標註#我愛溫博館。",
                    img_photo: require("@/assets/photo_01_flowertiles.jpg"),
                    img_paint: require("@/assets/img_01_flowertiles.jpg")
                },
                {
                    name: "日式鞋櫃",
                    id: 2,
                    info:
                        "進館要換拖鞋，鞋櫃特別從日本引進，每格的木頭鑰匙都不一樣。",
                    task: "請在地圖畫下你的鑰匙外觀。",
                    img_photo: require("@/assets/photo_02_shoebox.jpg"),
                    img_paint: require("@/assets/img_02_shoebox.jpg")
                },
                {
                    name: "日式和室",
                    id: 3,
                    info:
                        "二樓的榻榻米和室是泡湯完休息的地方，可以在望樓遠眺風景。",
                    task:
                        "坐在榻榻米上休息片刻，並在日記第7頁的和室俯瞰圖中，標示出你所在的位置。",
                    img_photo: require("@/assets/photo_03_tatami.jpg"),
                    img_paint: require("@/assets/img_03_tatami.jpg")
                },
                {
                    name: "瀧乃湯浴室",
                    id: 4,
                    info:
                        "新北投現存最古老的日式浴場，由鐵道部興建，又稱鐵之湯，位於北投溪一處布旁，屬於青磺泉質。",
                    task:
                        "浴室旁紀念碑所指的皇太子，是哪位皇太子呢？請在地圖中寫下。",
                    img_photo: require("@/assets/photo_04_bathroom.jpg"),
                    img_paint: require("@/assets/img_04_bathroom.jpg")
                },
                {
                    name: "噴水池",
                    id: 5,
                    info:
                        "水源是1911年完工的北投水道系統，讓泡湯客流汗排毒後，有乾淨飲水。",
                    task:
                        "噴水池中間曾經被偷走的銅製動物，像是什麼動物？請在地圖中寫下。",
                    img_photo: require("@/assets/photo_05_fountain.jpg"),
                    img_paint: require("@/assets/img_05_fountain.jpg")
                }
            ],
            list2: [
                {
                    name: "新北投車站",
                    id: 6,
                    info:
                        "1937年將候車亭擴建成室內月台。屋頂的銅瓦起初為紅色，因硫磺氣影響變成黑色。",
                    task:
                        "觀察屋頂的四扇窗戶，在地圖寫下為何它們彼此的間距不一致呢?",
                    img_photo: require("@/assets/photo_06_station.jpg"),
                    img_paint: require("@/assets/img_06_station.jpg")
                },
                {
                    name: "北投石",
                    id: 7,
                    info:
                        "沈積於其他岩石上，外觀微小呈白色的雞冠狀，含有『鐳』成分，是世界上唯一以台灣地名命名的礦物。",
                    task:
                        "溫博館的北投石旁的密碼1905 /98/300/80/800，分別代表甚麼意涵？在日記第8頁寫下。",
                    img_photo: require("@/assets/photo_07_stone.jpg"),
                    img_paint: require("@/assets/img_07_stone.jpg")
                },
                {
                    name: "北投圖書館",
                    id: 8,
                    info:
                        "臺灣首座綠建築圖書館，建物使用可回收再利用的木材及鋼材，屋頂為輕質生態屋頂，設太陽能光電板發電。",
                    task:
                        "在綠建築圖書區中，挑一本書閱讀，並在地圖中寫下書名，以及書中對於綠建築的描述。",
                    img_photo: require("@/assets/photo_08_library.jpg"),
                    img_paint: require("@/assets/img_08_library.jpg")
                },
                {
                    name: "地熱谷",
                    id: 9,
                    info:
                        "「北投」原為凱達格蘭族居住地，是女巫的意思。地熱谷煙霧繚繞，延伸出凱達格蘭族對於女巫的敬畏。",
                    task: "觀察地熱谷的形態，在地圖中畫下你心目中女巫的形象。",
                    img_photo: require("@/assets/photo_09_valley.jpg"),
                    img_paint: require("@/assets/img_09_valley.jpg")
                }
            ]
        };
    },
    methods: {
        showpop: function(item) {
            this.dialog = true;
            // $set(dialog, props.item.id, true),
            (this.dialogName = item.name),
                (this.dialogId = item.id),
                (this.dialogInfo = item.info),
                (this.dialogTask = item.task),
                (this.dialogImgPhoto = item.img_photo),
                (this.dialogImgPaint = item.img_paint);
        }
    }
};
</script>
<style>
.task.nolight {
    background: white;
}
</style>





