<template>
    <div id="app" data-app="true">
        <v-container>
            <h3 class="page_title">任務卡片
            </h3>
            <v-btn router-link class="rounterLink" to="/paint_light_down">A：手繪/任務下</v-btn>
            <v-btn router-link class="rounterLink" to="/paint_light_top">B：手繪/任務上</v-btn>
            <v-btn router-link class="rounterLink" to="/photo_light_down">C：相片/任務下</v-btn>
            <v-btn router-link class="rounterLink" to="/photo_light_top">D：相片/任務上</v-btn>

            <!-- <v-btn router-link class="rounterLink" to="/photo_nolight_down">照片/無標示/任務下</v-btn> -->
            <!-- <v-btn router-link class="rounterLink" to="/photo_nolight_top">照片/無標示/任務上</v-btn> -->
            <!-- <v-btn router-link class="rounterLink" to="/paint_nolight_down">插圖/無標示/任務下</v-btn> -->
            <!-- <v-btn router-link class="rounterLink" to="/paint_nolight_top">插圖/無標示/任務上</v-btn> -->
            <div class="space"></div>
            <router-view> </router-view>
        </v-container>
    </div>
</template>

<script>
export default {
    name: "App"
};
</script>

<style>
.rounterLink {
    margin: 6px 2px;
}
.space {
    height: 20px;
    width: 100%;
    display: block;
}
#app {
    font-family: "Avenir", Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 20px;
}
.page_title {
    margin-bottom: 20px;
}

/* 卡片樣式 */
.cus_card {
    display: flex;
}

.list-group {
    width: 100%;
    padding: 0px 5px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.list-group-item {
    border-radius: 25px;
    padding: 10px 15px;
    margin-bottom: 10px;
    box-shadow: 1px 1px 3px 1px #00000021;
    position: relative;
    padding-bottom: 120px;
    font-size: 13px;
    background: white;
    cursor: pointer;
    width: calc(50% - 5px);
}
.title {
    font-size: 1.6rem !important;
    font-weight: 600;
    margin-bottom: 5px;
}
.title span {
    z-index: 10;
    position: relative;
}

.title::after {
    content: "";
    background: #cdd8b8;
    height: 29px;
    width: 100%;
    position: absolute;
    top: 0;
    right: 0;
    border-radius: 20px 20px 0 0;
    z-index: 1;
    border-bottom: 1px dotted #e46c0a;
}
.task {
    width: 100%;
    height: 120px;
    position: absolute;
    bottom: 0;
    right: 0;
    border-radius: 0 0 20px 20px;
    background: #ffe24a;
    display: flex;
    align-items: center;
    justify-content: end;
    padding: 0 15px;
    text-align: left;
}

.v-image {
    margin: 10px 0;
    height: 160px;
}
.v-image.paint {
    width: 100%;
    height: auto;
}
/* dialog */
.v-dialog {
    border-radius: 25px !important;
}
.v-overlay__scrim {
    border-radius: 0 !important;
}
.v-card {
    padding: 10px 15px;
    padding-bottom: 120px;
    font-size: 13px;
    background: white;
}
span.info {
    text-align: start;
    display: flex;
}
</style>



